xed-table-loading\" style=\"top: 38px; display: none;\">\r\n                        <img src=\"/content/image/loading.gif\">\r\n                      </div>\r\n\r\n\r\n                      <el-table\r\n                        :data=\"storeusers\"\r\n                        style=\"width: 100%\"\r\n                        stripe\r\n                      >\r\n                        <el-table-column\r\n                          prop=\"name\"\r\n                          label=\"名字\"\r\n                        >\r\n                          <template slot-scope=\"scope\" >\r\n                              {{scope.row.name}}\r\n                          </template>\r\n                        </el-table-column>\r\n\r\n                        <el-table-column\r\n                          prop=\"mobile\"\r\n                          label=\"手机号码\"\r\n                        >\r\n                          <template slot-scope=\"scope\" >\r\n                              {{scope.row.mobile}}\r\n                          </template>\r\n                        </el-table-column>\r\n\r\n                        <el-table-column\r\n                          prop=\"departmentId\"\r\n                          label=\"所属店铺\"\r\n                        >\r\n                          <template slot-scope=\"scope\" >\r\n                              {{scope.row.shopManage.name}}\r\n                          </template>\r\n                        </el-table-column>\r\n\r\n                        <el-table-column\r\n                          prop=\"jobs\"\r\n                          label=\"职位\"\r\n                        >\r\n                          <template slot-scope=\"scope\" >\r\n                              {{scope.row.jobs}}\r\n                          </template>\r\n                        </el-table-column>\r\n\r\n                        <el-table-column\r\n                          prop=\"createTime\"\r\n                          label=\"新增时间\"\r\n                       